import React, { Component } from "react";
import styled from 'styled-components';
import Link from './ActiveLink'
import {NavButton} from './buttons';
import SearchForm from '../search/searchForm';
import {VERSION} from '../../actions/types'
import { BREAKPOINT_LG,BOX_SHADOW,ACTIVE_NAV_LIST_COLOR,BORDER_RADIUS,NAVBAR_BACKGROUND,NAV_LIST_COLOR,HEADER_BACKGROUND,SEARCH_BACKGROUND,NAVBAR_PADDING,NAVBAR_SCROLL_BACKGROUND,HEADER_COLOR,BLUE_COLOR,DARK_BORDER_COLOR,BORDER_COLOR_ACTIVE,GREEN_COLOR,LIGHT_BACKGROUND,MAX_WIDTH,BORDER_COLOR,SECONDARY_FONT_COLOR,FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM, FONT_FAMILY_HEADER, FADE_FONT_COLOR} from '../types';
const Dropdown = styled.div`
     position: absolute;
    background:${SEARCH_BACKGROUND};
    border-radius: 5px;
    color: #fff;

    &:before {
      content: "";
    right: 100%;
    bottom: 100%;
    position: absolute;
    margin-right: -33px;
    position: absolute;
    width: 0;
    height: 0;
    border: 7px solid transparent;
    border-top-width: 0;
    border-bottom-color:${SEARCH_BACKGROUND};
    }
    transition: all .25s cubic-bezier(.24,.22,.015,1.56),opacity .1s ease-in-out;
    top: 60px;
    max-width: 160px;
    opacity: 0;
    visibility:hidden;
    transform: scale(0.8) translateY(-10px);
    &.active {
          box-shadow: 0 1px 3px 0 rgba(0,0,0,0.08), 0 0 5PX 1px rgba(0,0,0,0.04);
    opacity: 1;visibility:visible;

    transform: scale(1) translateY(0);
}
    ul {
    list-style: none;
    display: block;
    text-align: left;
    
    padding: 12px 15px!important;
    li {
    display: block;
    font-size: 13px!important;
    line-height: 1.6!important;
    min-width: 130px;
    a{
      line-height:30px!important;
          height: auto!important;
    }

}
}
`;
const Navbar = styled.div`
  left: 0;
  right: 0;
 position: fixed;
 background:${NAVBAR_BACKGROUND};
  z-index: 99996 ;
  transition: all  200ms cubic-bezier(0.4, 0, 0.2, 1);
    top: 0;
  &.scroll {
    box-shadow: 0 4px 4px rgba(0,0,0,.06);
    background: ${NAVBAR_SCROLL_BACKGROUND};

    ul{
     
      li{&.nav-search{
        .search-sticky{
         display:block!important;visibility:visible!important;}}
        &.boxicon_brand{
    overflow: hidden;  
    .brand{
      
    svg {
    width: 100%;
}
    }
        }
       
      }
    }
}

      
ul li a.active{
          
        }
        ul li a:hover{
          color:
         ${BLUE_COLOR};
        }
    

  .navbar-max{ 
  margin: 0px auto;
    ul{
        
          transition: 0.15s;
          margin: 0;
    list-style: none;
    padding: 5px 20px;
    @media screen and (max-width: ${BREAKPOINT_LG}) {padding: 13px 20px!important;}
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
  
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
   
    li{
      input {
          height: 36px!important;
          line-height: 36px!important;
          font-size: 18px!important;
      }
      &.mobile-menu {
     
          order: 3;
          display: none;
           @media screen and (max-width: ${BREAKPOINT_LG}) {
            display: block;
          }
         
          @media screen and (max-width: ${BREAKPOINT_MD}) {
           a{
            font-size: 13PX!important;
            padding: 0 10px!important;

    height: 36px!important;
    line-height: 36px!important;
            span{display:none;}
           }
          }
        }
      &.nav-search {
        padding-left: 15px;
        flex: 1;
        .search-sticky{
        display:none;visibility:hidden;}

         @media screen and (max-width: ${BREAKPOINT_LG}) {
            display: none !important;
        }
      }
      &.nav-links {
        display: flex;
          text-align: right;
          padding: 0;
          order: 2;
          align-items: center;
    padding: 0 15px;
    padding-right:0px;
          .git-frame {
    display: flex;
    align-items: center;
    padding: 0px;
   
}
             a{
          
&.active{
  color:${ACTIVE_NAV_LIST_COLOR};
}
             }
         @media screen and (max-width: ${BREAKPOINT_LG}) {
            display: none !important;
        }
      }
      margin: 0px;
      line-height: 20px;
      font-size: 13px;
      padding: 0px;
      order: 0;
      &.boxicon_brand {   
       

       a{padding:0px;}

         @media screen and (max-width: ${BREAKPOINT_LG}) {
          flex: 1;
          height: 38px;
          padding:0 5px!important;
        }
        a{

          &:hover{
              background:transparent!important;
            }
          @media screen and (max-width: ${BREAKPOINT_LG}) {
          
          padding:0px;
        }
        }
    
      }

      
      .search-sticky {
        background: transparent;
        padding-bottom: 0px;
        padding: 0px;
        transition: 0.15s;
        transform: translateY(0px);
      
      }
      .search-cont {
    margin: 0px 0!important;
        max-width: none;
        margin-left: 0px!important;
    padding: 2px;
    display: flex;
    background:${LIGHT_BACKGROUND};
    border:1px solid ${LIGHT_BACKGROUND};
    box-shadow:0 0 0 0px rgba(0,0,0,0.1)!important;
    height:40px;
    margin: 0px;
    align-items: center;
    &.active{
      background:#fff;
      border:1px solid #ddd;
    }
      &.active input{
        color:#111!important;
      }
        i {
            font-size: 21px!important;
            line-height: 25px;
            padding: 0px 12px!important;
        }
        i.bx-x {
          font-size: 21px !important;
    padding: 3px !important;
    height: 21px !important;
    line-height: 21px !important;
          color: #73859d;
          border-radius: 50%;
          cursor: pointer;
          margin-right: 7px;
        }
        form {
    width: 100%;
}
      
        &.active {

          
        
        }
      }
      a{
         &.show-dropdown {
      cursor: pointer;
}
            font-weight: 400;
            border-radius:2px;
        &.mobile-menu-button {
          line-height: 24px !important;
          height: 24px !important;
              border-radius: 50%;
        padding: 0px 12px;
        }
        &:hover {
          color:${BLUE_COLOR};
              
        }
        i {
          font-size: 24px;
         
          line-height: 24px;
    height: 24px;
          vertical-align: middle;
        }

        .brand {
            height: 56px;
            @media screen and (max-width: ${BREAKPOINT_LG}) {
              height: 34px;
              width: 34px;
    }
    @media screen and (max-width: ${BREAKPOINT_MD}) {
              height: 34px;
              width: 34px;
    }
           svg {
    width: 100%; height: 40px;
}display: flex;
    align-items: center;
    width: 56px;
    margin: 1px 0;
    
 margin-right: 10px;
 transition:0.15s;
         &:hover{
          opacity:0.9;
         }
        
         
        }
        padding: 0px 15px;
        text-decoration: none;
        color: ${NAV_LIST_COLOR};
        height: 60px;
    line-height: 64px;
    border-radius: 0;

        position: relative;
        display: inline-block;
        transition: 0.15s;
        font-size: .875rem;
        font-weight:400;
      
      }
    }
  }}
`;

class NavigationBar extends Component {
   constructor(props) {
        super(props);
        this.state = {
            showMobile: false,
            showGum: false,
            showDropdown: false,
            is_Mounted:false,
        };

        this.handleMenuClick = this.handleMenuClick.bind(this);
        this.handleMenuLinkClick = this.handleMenuLinkClick.bind(this);
      this.handleNavbarScroll = this.handleNavbarScroll.bind(this);
        this.handleDropdown = this.handleDropdown.bind(this);
     
    }
   
    handleNavbarScroll(){
   var header = document.querySelector(".navbar-cont");
   
        var fixed_class = "scroll";

        if (window.pageYOffset >250) {
            header.classList.add(fixed_class);

        } else {
            header.classList.remove(fixed_class);
        }
    }
    handleMenuLinkClick(){
      this.setState({showMobile:false});
    }
    handleDropdown(e){
      e.preventDefault();
      this.setState({showDropdown:!this.state.showDropdown});
    }
    handleMenuClick(e){
      e.preventDefault();
      this.setState({showMobile:!this.state.showMobile});
    }
    componentDidMount() {
        setTimeout(
            function() {
              this.setState({is_Mounted:true})
        
            }
            .bind(this),
            10
        );
        window.addEventListener("scroll", this.handleNavbarScroll);
 
    }
    componentWillUnmount() {
       var header = document.querySelector(".navbar-cont");
       header.classList.remove("scroll");
        window.removeEventListener("scroll", this.handleNavbarScroll);
    }
    
    render() {

        return (
            <Navbar className="navbar-cont" >
              <div className="navbar-max">
                <ul className={this.state.is_Mounted?"navbar mounted":"navbar"} role="navigation">
                  <li className="boxicon_brand">
            <Link activeClassName="active" href="/">
           <a>
            <div className="brand sprite" >
 
            <svg version="1.1"  xmlns="http://www.w3.org/2000/svg"  x="0px" y="0px"
	 width="130px" height="130px" viewBox="241 2.497 130 130">
<path id="XMLID_1_" fill="#000" d="M252.505,47.253v39.23c0,6.275,3.138,11.508,8.892,14.646l39.753,21.97
	c3.662,2.092,8.369-0.521,8.369-4.707V66.083l46.555-23.538c4.185-2.093,4.185-7.847,0-9.938L315.273,12.73
	c-4.707-2.093-9.938-2.093-14.646,0L261.92,32.606C256.166,35.746,252.505,40.976,252.505,47.253z"/>
<path fill="#000" d="M255.12,38.884c-1.569,2.615-2.615,5.754-2.615,8.369v39.23c0,6.275,3.138,11.508,8.892,14.646l39.753,21.97
	c3.662,2.092,8.369-0.521,8.369-4.707V66.083l1.047-0.523L255.12,38.884z"/>
</svg>











</div></a>
            </Link>
          </li>
          <li className="nav-search">
            {this.props.page=='index'?<SearchForm/>:null}
          </li>
          <li className="nav-links">
         
          <Link activeClassName="active" href="/">
           <a>
           Icons</a>
            </Link>
           <a target="_blank" rel="noopener noreferrer" href="https://github.com/atisawd/boxicons">
           Github</a>
           
          
            <Link activeClassName="active"href="/usage"><a>
           Usage</a>
            </Link>
            <a target="_blank" rel="noopener noreferrer" href="https://paypal.me/atisawd">
           Donate</a>
           
            <NavButton rel="noopener noreferrer"  href="http://gum.co/boxicons" target="_blank"  >Download <span>v{VERSION}</span></NavButton>
     
            
           
          </li>
          <li className="mobile-menu">
          <NavButton download href={"/static/boxicons-"+VERSION+".zip"}  >Download <span>v{VERSION}</span></NavButton>
          </li>

        </ul></div>
      </Navbar>
        );
    }
}
export default NavigationBar;